import { shuffle } from './array'

export function createTrainingCardElements(picks, mode) {
  const [modeRiddle, modeOptions] = mode.split('-')
  let riddle = []
  let options = []

  if (modeRiddle === 'jp') {
    riddle = 
      <div className='text-8xl'>
        { picks[0].jp }
      </div>
  } else if (modeRiddle === 'en') {
    riddle =
      <div className='flex flex-col'>
        <div className='flex justify-center items-center h-10 text-xl'>
          <b>{ picks[0].en }</b>
        </div>
        {
          (picks[0].onyomi || picks[0].kunyomi) &&
            <div className='flex flex-col justify-center items-center h-40 border-indigo-600'>
              <div className='mb-4 text-center'>
                Onyomi:
                <br/>
                <b>{ picks[0].onyomi || <>&mdash;</> }</b>
              </div>
              <div className='text-center'>
                Kunyomi:
                <br/>
                <b>{ picks[0].kunyomi || <>&mdash;</> }</b>
              </div>
            </div>
        }
      </div>
  }

  if (modeOptions === 'en') {
    options = shuffle(picks.map((opt, idx) => ({
      isCorrect: !idx,
      jsx: <>
        {
          (opt.onyomi || opt.kunyomi) &&
            <div className='flex flex-col justify-center items-center h-40 border-b border-indigo-600'>
              <div className='mb-4 text-center'>
                Onyomi:
                <br/>
                <b>{ opt.onyomi || <>&mdash;</> }</b>
              </div>
              <div className='text-center'>
                Kunyomi:
                <br/>
                <b>{ opt.kunyomi || <>&mdash;</> }</b>
              </div>
            </div>
        }
        <div className='flex justify-center items-center h-10'>
          <b>{ opt.en }</b>
        </div>
      </>
    })))
  } else if (modeOptions === 'jp') {
    options = shuffle(picks.map((opt, idx) => ({
      isCorrect: !idx,
      jsx:
        <div className='p-4 text-3xl text-center'>
          { opt.jp }
        </div>
    })))
  }

  const indexOfCorrect = options.findIndex(opt => opt.isCorrect) 

  return [{ riddle, options }, indexOfCorrect]
}