import cn from 'classnames';
function TrainingCard({
  data = [],
  checkAnswerHandler,
}) {
  return (
    <div
      className='border border-indigo-600 rounded w-full'
    >
      <div className='flex justify-center items-center h-60 p-1'>
        { data.riddle }
      </div>
      <div className='flex border-t border-indigo-600'>
        {
          data.options.map((option, idx) => (
            <div
              key={idx}
              className={cn(
                'w-full cursor-pointer hover:bg-indigo-100 p-1',
                { 'border-l border-indigo-600': idx > 0 }
              )}
              onClick={() => checkAnswerHandler(option.isCorrect, idx)}
            >
              { option.jsx }
            </div>    
          ))
        }
      </div>
    </div>
  )
}

export { TrainingCard }