import cn from 'classnames'
function LearningCard({
  data,
  blind,  
}) {
  return (
    <div className='group border border-indigo-600 rounded hover:bg-indigo-100 w-full px-2 cursor-pointer'>
      <div
        className={cn(
          {
            'opacity-0': blind
          },
          'group-hover:opacity-100 transition-all duration-200 flex justify-center text-8xl m-4'
        )}
        style={{willChange: 'opacity'}}
      >
        { data.jp }
      </div>
      {
        (data.onyomi || data.kunyomi) &&
          <div className='py-2 border-t border-b border-indigo-600'>
            <div>
              Onyomi: <b>{ data.onyomi || <>&mdash;</> }</b>
            </div>
            <div>
              Kunyomi: <b>{ data.kunyomi || <>&mdash;</> }</b>
            </div>
          </div>
      }
      <div className='py-1 text-center'>
        <b>{ data.en }</b>
      </div>
    </div>
  )
}

export { LearningCard }